@import url(https://fonts.googleapis.com/css?family=Nunito:300,400,600,700,800);
body {
  margin: 0;
  font-family: "Nunito", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding: 20px 16px; }

.form__todo {
  box-sizing: border-box;
  width: 100%;
  padding-bottom: 32px; }
  .form__todo .inputBox {
    margin-bottom: 16px; }
    .form__todo .inputBox label {
      display: block;
      font-size: 15px;
      color: #e0e0e0; }
    .form__todo .inputBox .inputBox__field {
      box-sizing: border-box;
      display: block;
      width: 100%;
      height: 44px;
      margin-top: 10px;
      padding: 0 12px;
      font-size: 16px;
      border: 1px solid #c5c5c5;
      border-radius: 8px; }
      .form__todo .inputBox .inputBox__field:focus {
        outline: none;
        border-width: 2px; }

.error__msg {
  margin-top: 4px;
  font-size: 14px;
  font-weight: 700;
  color: #e20c0c; }

.error__input {
  border: 2px solid #e20c0c !important; }

.modal {
  box-sizing: border-box;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  padding: 0 32px;
  background: rgba(35, 33, 33, 0.9); }
  .modal--wrapper {
    display: flex;
    flex-flow: column wrap;
    justify-content: flex-end;
    width: inherit;
    height: inherit;
    -webkit-transform: translateY(0);
            transform: translateY(0); }
    .modal--wrapper .btn--close {
      order: -1;
      width: 50px;
      height: 24px;
      align-self: flex-end; }

.btn--show {
  position: absolute;
  right: 32px;
  bottom: 40px;
  width: calc(100% - 64px);
  padding: 10px 0;
  border: 2px solid #0079ff;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #0079ff; }
  .btn--show:hover {
    background: #0079ff;
    color: #fff;
    -webkit-transition: all 0.22s cubic-bezier(0.19, 0.79, 0.88, 0.29);
    transition: all 0.22s cubic-bezier(0.19, 0.79, 0.88, 0.29); }

.show {
  display: block; }

.hide {
  display: none; }

.show--modal {
  -webkit-animation: showModal .4s ease-in;
          animation: showModal .4s ease-in; }

.hide--modal {
  -webkit-animation: showModal .4s ease-in backwards;
          animation: showModal .4s ease-in backwards; }

@-webkit-keyframes showModal {
  0% {
    -webkit-transform: translateY(25%);
            transform: translateY(25%); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes showModal {
  0% {
    -webkit-transform: translateY(25%);
            transform: translateY(25%); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

.title {
  margin: 0;
  padding: 0;
  padding-bottom: 8px;
  font-size: 28px;
  color: #2e2e2e; }

.todolist {
  padding: 16px; }
  .todolist .todolist__list {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    width: 100%;
    height: 56px;
    padding: 0 8px;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    border-radius: 8px;
    list-style: none; }
    .todolist .todolist__list .text {
      font-size: 17px;
      color: #2e2e2e; }
    .todolist .todolist__list .todolist__title {
      padding-left: 10px; }
      .todolist .todolist__list .todolist__title .todolist__category {
        margin: 0;
        font-size: 11px;
        color: #757575; }
    .todolist .todolist__list .todolist__checkbox {
      order: -1;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 24px;
      height: 24px;
      margin-left: 8px;
      border: 1px solid #000;
      border-radius: 4px; }
      .todolist .todolist__list .todolist__checkbox::before {
        content: '';
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%); }
      .todolist .todolist__list .todolist__checkbox:checked::before {
        width: 10px;
        height: 6px;
        margin-top: -5px;
        border-left: 2px solid;
        border-bottom: 2px solid;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg); }
  .todolist .todo--done .text {
    opacity: 0.4;
    text-decoration: line-through;
    -webkit-transition: all 0.14s ease-out;
    transition: all 0.14s ease-out; }
  .todolist .btn--delete {
    margin-left: auto;
    color: #e20c0c;
    border: 1px solid #e20c0c;
    border-radius: 4px; }

ul {
  margin: 0;
  padding: 0; }

a {
  color: inherit;
  text-decoration: none; }

p {
  margin: 0; }

input[type="checkbox"] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: none;
  border: 0;
  cursor: pointer; }

